import NormalOrder from '@/components/Cart/Platforms/mini/normal'
import RPOrder from '@/components/Cart/Platforms/mini/rp-order'

export default {
  components: {
    NormalOrder,
    RPOrder
  },

  props: {
    expanddetail: {
      type: Function,
      required: true
    },
    updatecache: {
      type: Function,
      required: true
    },
    expansion: {
      type: Array,
      required: true
    },
    subsorder: {
      type: Number,
      required: true
    },
    normalorder: {
      type: Number,
      required: true
    },
    changeqty: {
      type: Function,
      required: true
    },
    parsedate: {
      type: Function,
      required: true
    },
    parseprice: {
      type: Function,
      required: true
    },
    changeqtysubsitem: {
      type: Function,
      required: true
    },
    deleterp: {
      type: Function,
      required: true
    },
    selectsubsdate: {
      type: Function,
      required: true
    },
    changehandler: {
      type: Function,
      required: true
    },
    changenotes: {
      type: Function,
      required: true
    },
    changedeliverytime: {
      type: Function,
      required: true
    },
    changedeliverysingletime: {
      type: Function,
      required: true
    },
  },

  computed: {
    delivery_time_normal() {
      return this.$store.state.delivery_time_normal
    },

    store() {
      return this.$store.state.store
    },

    rp_order () {
      return this.$store.state.rp_order
    },

    cart () {
      return this.$store.state.cart
    },

    notes () {
      return this.$store.state.user_notes
    },

    dates () {
      return this.$store.state.dates
    }
  },
}
